@import "https://fonts.googleapis.com/css2?family=Roboto:wght@500&display=swap";
@import "https://fonts.googleapis.com/css2?family=DM+Sans:wght@700&display=swap";
.header {
  color: #fff;
  height: 84px;
  background-color: #0c111b;
  justify-content: space-between;
  align-items: center;
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 500;
  display: flex;
}

.header__img {
  width: 67px;
  height: 63px;
  padding-left: 30px;
}

.nav {
  justify-content: space-around;
  align-items: center;
  display: flex;
}

.nav__item {
  height: 50px;
  align-items: center;
  margin-right: 30px;
  padding-left: 8px;
  padding-right: 8px;
}

.nav__item_link {
  padding-right: 50px;
  position: relative;
  top: 35%;
  left: 20%;
}

.nav__item:hover {
  border-radius: 15px;
  transition: all .5s;
  box-shadow: 5px 5px 15px #fff;
}

.nav__item_active {
  border-top: 1px solid #000;
  border-bottom: 1px solid #000;
}

img {
  max-width: 100%;
  display: block;
}

ul {
  margin: 0;
  padding: 0;
  list-style: none;
}

a {
  color: #fff;
  text-decoration: none;
}

.search {
  width: 50px;
  height: 50px;
  box-sizing: border-box;
  background: #fff;
  border: 4px solid #fff;
  border-radius: 25px;
  transition: all 1s;
  position: relative;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.inputsearch {
  width: 100%;
  height: 42.5px;
  border: 0;
  border-radius: 20px;
  outline: 0;
  padding: 0 20px;
  font-size: 14px;
  line-height: 30px;
  display: none;
  position: absolute;
  top: 0;
  left: 0;
}

.fa {
  box-sizing: border-box;
  width: 42.5px;
  height: 42.5px;
  color: #0c111b;
  text-align: center;
  border-radius: 50%;
  padding: 10px;
  font-size: 18px;
  transition: all 1s;
  position: absolute;
  top: 0;
  right: 0;
}

.search:hover {
  width: 300px;
  cursor: pointer;
}

.search:hover input {
  display: block;
}

.search:hover .fa {
  color: #fff;
  background: #07051a;
}

.modal {
  width: 100%;
  height: 80vh;
  opacity: 0;
  pointer-events: none;
  background: #00000080;
  margin-top: 20px;
  transition: all .5s;
  position: fixed;
  top: 0;
  left: 0;
}

.modal.active {
  opacity: 1;
  pointer-events: all;
  transition: all .5s;
}

.close-modal {
  cursor: pointer;
  position: absolute;
  top: 10px;
  right: 10px;
}

.reg {
  width: 900px;
  height: 500px;
  min-height: 80vh;
  background-color: #420b0b;
  justify-content: center;
  align-items: center;
  margin: 0;
  transition: all .5s;
  display: flex;
  position: absolute;
  left: 25%;
}

.reg.active {
  background-color: #101266;
}

.container {
  width: 800px;
  height: 500px;
  outline: solid;
  padding: 40px 0;
  position: relative;
}

.block {
  height: 100%;
  background-color: #fff3;
  align-items: center;
  display: flex;
  box-shadow: 0 10px 50px #fffc;
}

.block_item {
  width: 50%;
}

.block-item {
  text-align: center;
}

.block-item_title {
  color: #fff;
  margin-bottom: 10px;
  font-size: 1.2rem;
  font-weight: 500;
}

.block-item_btn {
  cursor: pointer;
  color: #333;
  background-color: #fff;
  border: none;
  padding: 10px 20px;
  font-size: 18px;
  font-weight: 500;
}

.form-box {
  height: 100%;
  width: 50%;
  z-index: 100;
  background-color: #fff;
  transition: all .5s ease-in-out;
  display: flex;
  position: absolute;
  top: 0;
  left: 0;
  overflow: hidden;
  box-shadow: 0 5px 45px #fffc;
}

.form-box.active {
  left: 50%;
}

.form {
  width: 100%;
  padding: 50px;
  font-size: 16px;
  transition: all .5s;
  position: absolute;
}

.form_signin {
  transition-delay: .25s;
  left: 0;
}

.form_signup {
  transition-delay: 0s;
  left: 100%;
}

.form-box.active .form_signin {
  transition-delay: 0s;
  left: -100%;
}

.form-box.active .form_signup {
  transition-delay: .25s;
  left: 0;
}

.form_title {
  color: #333;
  font-size: 1.5rem;
  font-weight: 500;
}

.form_input {
  width: 70%;
  border: 1px solid #333;
  padding: 10px;
}

.form_btn {
  cursor: pointer;
  color: #fff;
  background-color: #272b49;
  border: none;
  padding: 10px 50px;
  font-size: 1.5rem;
}

.form_forgot {
  color: #272b49;
  text-decoration: none;
}

.error {
  color: red;
  top: 0;
}

.wrapper {
  color: #fff;
  background-color: #0c111b;
  background-size: cover;
  font-family: DM Sans, sans-serif;
  font-size: 18px;
}

.footer-list-title {
  text-align: center;
  padding-bottom: 25px;
  font-size: 18px;
}

.footer-list-item {
  align-items: center;
  list-style: none;
  display: flex;
}

.footer-list-social {
  justify-content: space-around;
  align-items: center;
  gap: 48px;
  padding-left: 30%;
  padding-right: 30%;
  display: flex;
}

.footer-link-fb {
  width: 24px;
  height: 24px;
  background-image: url("../Vector.96a81abd.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.footer-link-instagram {
  width: 24px;
  height: 24px;
  background-image: url("../instagram.c24badb4.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.footer-link-twitter {
  width: 24px;
  height: 19.5px;
  background-image: url("../twitter.7e3f26c7.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.footer-link-youtube {
  width: 22.76px;
  height: 16px;
  background-image: url("../youtube.a4e9a05a.svg");
  background-position: center;
  background-repeat: no-repeat;
}

.footer__nav {
  justify-content: space-around;
  align-items: center;
  gap: 48px;
  padding-left: 400px;
  padding-right: 400px;
  display: flex;
}

.footer-data {
  text-align: center;
  padding-top: 24px;
  padding-bottom: 24px;
  font-size: 18px;
}

@media screen and (min-width: 768px) {
  .footer, .footer-list {
    max-width: 1440px;
    margin: 0 auto;
    padding-top: 58px;
  }
}

@media screen and (max-width: 767px) {
  .footer {
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: flex-start;
    margin: 0 20px;
    padding-top: 58px;
    display: grid;
  }

  .contacts {
    grid-row: 5;
  }

  .social_media {
    grid-row: 7;
  }
}

